.skill-bar {
  position: relative;
  background-color: #f8f9fa;

  width: 90%;
  border-radius: 5px;
  height: 38px;
}

.animate-skill-bar {
  position: absolute;
  height: 36px;
  width: 100%;
  border-radius: 5px;
  background-image: linear-gradient(45deg, #a8c42c, rgb(75, 185, 75));
  animation: animate 3s ease-in-out forwards;
}
@keyframes animate {
  from {
    width: 0%;
  }
  to {
    width: 80%;
  }
}

.skill-percentage {
  position: absolute;
  right: 10px;
  line-height: 36px;
}

